<template>
  <div class="order">
    <div class="container">

      <!--顶部返回-->
      <div class="top-navBack">
        <p><span><</span>结算</p>
      </div>

      <!--商品件数-->
      <div class="content" v-for="(list,index) in stylelists">
        <div class="listImg">
          <img :src="list.img" alt="">
        </div>
        <div class="listsInfo">
          <p>{{list.name}}</p>
          <p>{{list.color}}{{list.size}}</p>
          <p>￥ {{list.price}}</p>
        </div>
        <div class="listCount">
          <p><span>x</span> {{list.num}}</p>
          <p><span>折扣:</span> {{list.discount}}</p>
          <p><span>结算价:￥</span> {{list.price}}</p>
        </div>
      </div>

      <!--商品结算-->
      <div class="countAll" v-for="(item,index) in countAll ">
        <div class="left">
          <p>购买数量</p>
          <p>商品原价</p>
          <p>现金优惠</p>
        </div>
        <div class="right">
          <p>{{item.total}}</p>
          <p>￥ {{item.jprice}}</p>
          <p>{{item.gprice}}</p>
        </div>
      </div>

      <!--最终计算-->
      <div class="countFinal" v-for="(item,index) in countAll ">
        <div>支付金额</div>
        <div>￥ {{item.pay}}</div>
      </div>

      <!--底部-->
      <div class="userInfo" v-for="item in userInfo">
        <div class="left">
          <div class="user">
            {{item.username}}
            {{item.mobile}}
          </div>
          <div class="address">
            {{item.address}}
          </div>
        </div>
        <div class="right">
          >
        </div>
      </div>
    </div>

    <div class="pay">
      <div class="submit"><a href="#">支付</a></div>
    </div>
  </div>


</template>

<script>
  export default {
    name: "Order",
    data () {
      return {
        stylelists: [],
        countAll: [],
        userInfo: []

      }
    },
    created: function () {
      this.$axios.get('../../static/api/shop.json')
      .then(res => {
        this.stylelists = res.data.stylelist;
        this.countAll = res.data.price;
        this.userInfo = res.data.userinfo;
      })
      .catch(function (error) {
        console.log(error);
      })
    }
  }

</script>

<style scoped>
  .container {
    display: flex;
    flex-direction: column;
    margin: 20px;
    background: #f8f8f8;
    border: 2px solid #eeeeee;
  }

  .top-navBack {
    height: 80px;
    width: auto;
    padding: 20px;
    display: flex;
    align-items: center;
    font-size: 30px;
    color: #ffffff;
    background: #323b4a;
  }

  .top-navBack span {
    font-size: 40px;
    padding-right: 10px;
  }

  .container .content {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #eeeeee;
    align-items: center;
    padding: 10px;
    background: #ffffff;
  }

  .content .listsInfo {
    display: flex;
    flex-direction: column;
    font-size: 30px;
  }

  .content .listImg img {
    width: 150px;
    height: 180px;
  }

  .content .listCount {
    display: flex;
    flex-direction: column;
    text-align: right;
    flex: 1;
    font-size: 30px;
  }

  .content .listCount span {
    font-size: 26px;
    color: #9999a5;
  }

  /*商品计算*/
  .countAll {
    display: flex;
    justify-content: space-between;
    font-size: 30px;
    padding: 20px;
    margin-top: 20px;
    background: #ffffff;
    color: #666666;
  }

  .countAll .right {
    text-align: right;
  }

  .countFinal {
    display: flex;
    padding: 20px;
    margin: 10px 0;
    flex-direction: row;
    font-size: 30px;
    background: #ffffff;
    justify-content: space-between;
  }

  /*用户信息*/
  .userInfo {
    display: flex;
    flex-direction: row;
    padding: 20px;
    font-size: 30px;
    align-items: center;
    align-content: center;
    background: #ffffff;
  }

  .userInfo .left {
    display: flex;
    flex-direction: column;
    align-items: left;
  }

  .userInfo .left .user {
    display: flex;
    padding: 20px 0;
    flex-direction: row;
  }

  .userInfo .left .address {
    display: flex;
    flex-direction: row;
    font-size: 24px;
    color: #cdcdcd;
  }

  .userInfo .right {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 60px;
    color: #d6d6d6;
    text-align: right;
  }

  .pay {
    width: auto;
    height: 260px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background:#e5e5e5;
  }

  .pay .submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 600px;
    height: 80px;
    background: #333333;
  }

  .pay a {
    color: #ffffff;
    font-size: 45px;
  }

</style>


